.cardimage {
  width: 10rem;
}
.card {
  align-items: center;
  padding: 10px;
  margin: 10px;
  width: 80vw;
}
.aboutcontainer {
  text-align: -webkit-center;
}
.slideinbottom {
  animation: slide-in-bottom 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}
.textshadowdropright {
  animation: text-shadow-drop-right 0.5s 1s both;
}
@keyframes slide-in-bottom {
  0% {
    -webkit-transform: translateY(50px);
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
    opacity: 1;
  }
}
@keyframes text-shadow-drop-right {
  0% {
    transform: translateX(0);
    filter: drop-shadow(0px 0px 0px);
  }
  100% {
    transform: translateX(-5px);
    filter: drop-shadow(5px 5px 5px rgb(170, 170, 170));
  }
}
