.container {
  background-color: rgb(243, 243, 243);
  margin: 15px 5px;
  font-size: 1.2em;
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgb(185, 185, 185);
}
.side {
  font-size: 1.3em;
  overflow-wrap: anywhere;
  padding: 0px 20px;
  width: 70%;
  color: blue;
  font-weight: 600;
}
.right{
    width: 30%;
    padding: 0px 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.date{
    font-size: 1rem;
    height: 30px;
    color: rgb(138, 0, 0);
}
.price{
    font-size: 1.5rem;
    color: rgb(3, 2, 102);
    font-weight: 600;
}