.main {
  display: flex;
  gap: 10px;
}
.leftpane {
  display: flex;
  flex-direction: column;
}
.list {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.listblock {
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  padding: 10px;
  width: 60%;
  background-color: rgb(241, 241, 241);
  align-items: center;
  box-shadow: 1px 1px 9px rgb(201, 201, 201);
}

.listblock .title {
  font-size: 1.3rem;
  text-transform: capitalize;
  color: blue;
  font-weight: bold;
}
.listblock .section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.listblock .section div:nth-child(1){
    font-size: 1.2rem ;
    font-weight: bold;
}
.listblock .section div:nth-child(1)::before{
    content: "Rs.";
}

.showjson{
    border: 1px solid black;
    width: 50%;
    padding: 10px;
    border-radius: 10px;
    background-color: rgb(231, 231, 231);
}
.showjson p{
    margin: 3px;
    font-family: 'Courier New', Courier, monospace;
}