.inputcontainer {
  display: flex;
  margin: 80px 0px 14px 0px;
  width: 70%;
  align-items: center;
}
.filtercontainer {
  display: flex;
  border-radius: 10px;
  background-color: white;
  width: 100%;
  padding: 10px;
  box-shadow: 1px 1px 8px lightgray;
}
.filtercontainer i {
  font-size: 1.4rem;
  background-color: white;
  color: grey;
}
.filtercontainer input {
  outline: none;
  border: none;
  padding: 0px 10px;
  font-size: 1rem;
  background-color: white;
  width: 100%;
  
}
.sum {
  margin: 10px;
  font-size: 1.5rem;
  border: 1px solid lightgray;
  width: max-content;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 8px lightgray;
  text-wrap-mode:nowrap;
}
.sum span {
  color: green;
}
.sum span::before {
  content: "Rs.";
}
.filterarray {
  display: flex;
  width: 80vw;
  flex-wrap: wrap;
}
.filterarray div {
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(28, 95, 204);
  margin: 10px;
  padding: 5px;
  border-radius: 10px;
  box-shadow: 1px 1px 3px rgb(86, 128, 196);
  align-items: center;
}
.filterarray div span:first-child {
  font-size: 1.2rem;
}
.filterarray div span:nth-child(2) {
  color: blue;
}
