.chartcontainer {
  width: 99vw;
  height: 50vh;
  border: 1px solid rgb(156, 156, 156);
  border-radius: 5px;
  box-shadow: 3px 4px 7px rgb(172, 172, 233);
}
.chartcontainer canvas{
  height: 100% !important;
  width:  100% !important;
}
@media (min-width: 400px) {
  .chartcontainer {
    width: 70vw;
    height: 70vh;
    border: 1px solid rgb(156, 156, 156);
    border-radius: 5px;
    box-shadow: 3px 4px 7px rgb(172, 172, 233);
  }
}
